import React from 'react';
import Slideshow from './Slideshow';
import { Main, Slide, TextoSlide } from '../styledcomponents/SlideshowItems';
import { Title } from '../styledcomponents/News';
import ex1 from '../images/2.jpeg';
import ex2 from '../images/3.jpeg';
import { Route } from 'react-router';
import { BrowserRouter, Link } from 'react-router-dom';
import FullGallery from './FullGallery';
const Gallery = () => {
	return (
		<BrowserRouter>
			<Route path='/'>
				<Main>
					<Title>Galeria</Title>
					<Slideshow controles={true} autoplay={true} velocidad="1500" intervalo="5000">
						<Slide>
							<a href="#d">
								<img src={ex1} alt="" />
							</a>
							<TextoSlide colorFondo="navy">
								<p>Semana de las Artes Sec. 5</p>
							</TextoSlide>
						</Slide>
						<Slide>
							<a href="#d">
								<img src={ex2} alt="" />
							</a>
							<TextoSlide>
								<p>Educaci√≥n Artistica, Pl√°stica</p>
							</TextoSlide>
						</Slide>
					</Slideshow>
				</Main>
			</Route>
				<div style= {{
					display: 'flex',
					justifyContent: 'center'
				}}>
					<Link to='/fullgallery' style={{background: '#ff9900', padding: '40px', textDecoration: 'none', fontWeight: '600', fontSize: '24px', color: '#000', margin: '20px 0'}}>Ver Todas</Link>
				</div>
				<Route path='/fullgallery'>
				<h1 style={{textAlign: 'center', marginBottom: '50px'}}>Galeria completa </h1>
				<FullGallery />
				</Route>
		</BrowserRouter>
	);
}



export default Gallery;